import React , {Component} from 'react'; 
import MenuBar from './components/MenuBar/menuBar';
import DangKy from './components/DangKy/dangKy';
import DangNhap from './components/DangNhap/dangNhap';
import KhoaHoc from './components/KhoaHoc/KhoaHoc';
import Course from './components/KhoaHoc/Course';
import {LoginProvider} from './context/login.context';
import Footer from './components/Footer/Footer';
import AddCourse from './components/KhoaHoc/AddCourse';
import TrangChu from './components/TrangChu/TrangChu';
import { Redirect } from 'react-router-dom';



import {BrowserRouter as Router, Route, Link} from 'react-router-dom';
class App extends Component{
  constructor(props)
  {
    super(props);
    this.state={logged:false,redirect:false};
    this.fnlogged = this.fnlogged.bind(this);
    this.setRedirect = this.setRedirect.bind(this);
  } 

  fnlogged(bien)
  {
    
    this.setState({...this.state,
        logged:bien,Redirect:true
        });

    

  }

  setRedirect()
  {
    let currentState = this.state;
    this.setState({...currentState,redirect:true});
  }
  renderRedirect()
  {
    if(this.state.redirect === true)
    {
      return (<Redirect to='/dangnhap' />)
    }
  }


  render()
  {
    return (
     
      <Router>
        <MenuBar logged={this.state.logged} fnlogged={this.fnlogged} fnSetRedirect={this.setRedirect}/>
        
        <Route path="/course/add" component={AddCourse}/>
        <Route path="/course"    render={(props)=>
          <Course {...props} logged={this.state.logged} renderRedirect={ this.renderRedirect}/>
        }/>

          <Route path="/congdong" component={()=>
          {
            return(<div>Cộng Đồng</div>);
          }}/>

          <Route path="/"  exact component={TrangChu}/>
          

          <Route path="/dangky" render={(props)=>
              <DangKy {...props}
              fnlogged={this.fnlogged}
              />
          }/>

          <Route path="/dangnhap" render={(props)=>
            <DangNhap {...props} 
            fnlogged={this.fnlogged}
            />
          }/>
          
          <Footer/>
          
        
      </Router>

      
      )
  }
}

export default App;



